<template>
  <div class="wrapper">
    <div class="content">
      <div class='c-left'>
        <ctitle :title="'地点分布'"></ctitle>
        <div class="inner">
          <Graph :params="ddfbParams"></Graph>
        </div>
      </div>
      <div class='c-right'>
        <div class='c-r-top'>
          <div class='charts'>
            <ctitle :title="'登记类型分布'"></ctitle>
            <div class='chart'>
              <loopPieNew :params="peoplePieParams"></loopPieNew>
            </div>
          </div>
          <div class='charts'>
            <ctitle :title="'倒流人数'"></ctitle>
            <div class='chart'>
              <loopPie :params="tswtPieParams"></loopPie>
            </div>
          </div>
        </div>
        <div class='c-r-bottom'>
          <ctitle :title="'信访量地区分布'"></ctitle>
          <!-- <div v-if="countyBackShow" class="map-back" @click="mapBack"></div> -->
          <div class="chart">
            <barline :params="xflphBarParams"></barline>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/BarLine";
  import loopPieNew from "@/components/pie/LoopPie1";
  import Graph from '@/components/graph/graph.vue'
  
  export default {
    name: "dsf",
    components: {
      ctitle,
      loopPie,
      barline,
      Graph,
      loopPieNew
    },
    data(){
      return{
        // 1.地点分布
        ddfbParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: []
        },
        // 2.登记类型分布
        peoplePieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            labelType: true,
            unit: "人",
            colors: ['#1ba1ff', '#4eed99', '#13f0ff'],
            borderColor: '#050e27',
						borderWidth: 5,
						minAngle: 15,
            // colors: ['', '', '', '', ''],
            // radius: ["30%", "65%"],
            radius: ["40%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: false
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true,
          }
        },
        // 3.倒流人数
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            twoLevel: true,
            unit: "人",
            colors: [ ['#6f67ff','#5eceff'], ['#fca82a', '#f5e350']],
            radius: ["41%", "49%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.15),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.2)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 0,
            borderColor: "#0B2244",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            tooltip: {
              show: false
            },
            rich: {
              bfontsize: "10%" //文字大小
            },
            labelSize: "22%", // label字号
            // threeLevel:true,
            rollFlag: true
          }
        },
        // 区划排名
        xjName:[],
        countyBackShow:false,
        // curCity: "广东省",
        // shijCity: "",
        // xjCity: "",
        // shej_02: "广东省",
        // shij_02: "invalid",
        // xj_02: "invalid",
        // nameP:'invalid',
        xflphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "0%",
            right: "10%",
            top: "5%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "25%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
      }
    },
    mounted() {
      this.getAPI(this.year)
    },
    methods:{
      getAPI(year){
        // 1.地点分布
        get_http("rdmxfxqtdsfddfb", {
        year:year,
        })
        .then(res => {
          this.ddfbParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 2.登记类型分布
        get_http("rdmxfxqtdsfxrsgmfb", {
          year:year
          }).then(res => {
            this.peoplePieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.倒流人数
        get_http("rdmxfxqtdsfxtswtfb", {
          year:year,
          })
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      // 4.信访量地区分布
        get_http("rdmxfxqtdsfxfldqfb", {
          year:year,
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val)
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .c-left
        flex 1
        // padding .3rem .4rem
        .inner
          padding 0.3rem
          width 100%
          height 100%
      .c-right
        flex 1
        display flex
        flex-direction column
        .c-r-top
          flex 1
          display flex
          justify-content center
          .charts
            flex 1
            display flex
            flex-direction column
            .m-title
              padding-left .5rem
            .chart
              flex 1
        .c-r-bottom
          flex 1
          display flex
          flex-direction column
          position relative
          .m-title
            padding-left .5rem
          .map-back
            position absolute
            top 30px
            right 60px
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            z-index 1000
            font-size 0.2rem
            color #fff
            cursor pointer
          .chart
            flex 1
      
      
</style>
